.home-container-container {
	position: absolute;
	background-color: rgb(14, 12, 12);
	position: absolute;
	height: 100%;
	background-image: url('../Images/background-vid.gif');
	background-repeat: no-repeat;
	background-size: cover;
	z-index: -1;
}
.home-body {
	height: 100%;
	background-color: rgba(0, 0, 0, 0.726);
	display: flex;
	flex-direction: column;
	justify-content: center;
	opacity: 1;
	filter: brightness(90%);
}
.logo {
	width: 50%;
	align-self: center;
	margin-bottom: 20px;
	margin-top: -100px;
	transition: all 1s ease-in-out;
	background-attachment: s;
}
.small-logo {
	width: 15%;
	min-width: 200px;
	align-self: center;
	margin-top: -40px;
	margin-bottom: 20px;
	transition: all 1s ease-in-out;
}
.buttons-container {
	/* position: absolute; */
	z-index: 10;
	margin-top: 20px;
	align-self: center;
	display: flex;
	flex-direction: row;
	width: 35%;
	justify-content: space-between;
	transition: all 1s ease-in-out;
}
.button {
	color: rgb(252, 184, 106);
	background-color: transparent;
	line-height: 30px;
	width: 150px;
	text-align: center;
	font-size: 14px;
	font-weight: bold;
	letter-spacing: 4px;
	border: 1px solid transparent;
	transition: all 0.35s ease-in-out;
}
.button:hover {
	background-color: rgb(252, 184, 106);
	letter-spacing: 4px;
	color: rgb(51, 31, 31);
	line-height: 30px;
	width: 150px;
	text-align: center;
	border-radius: 35px;
	font-size: 14px;
	border: 1px solid transparent;
	cursor: pointer;
	transition: all 0.35s ease-in-out;
}
.button:active {
	background-color: rgb(214, 181, 143);
	color: rgb(83, 23, 23);
	letter-spacing: 4px;
	line-height: 30px;
	width: 150px;
	text-align: center;
	border-radius: 35px;
	font-size: 14px;
	border: 1px solid transparent;
	cursor: pointer;
	transition: all 0.35s ease-in-out;
}
.button:focus {
	outline: 0;
}
.highlight-button {
	background-color: rgb(226, 204, 176);
	letter-spacing: 4px;
	color: rgb(141, 37, 37);
	line-height: 30px;
	width: 150px;
	text-align: center;
	border-radius: 35px;
	font-weight: bold;
	font-size: 14px;
	border: 1px solid transparent;
	cursor: pointer;
	transition: all 0.35s ease-in-out;
}
.highlight-button:focus {
	outline: 0;
}
.content {
	/* position: absolute; */
	height: 70%;
	align-self: center;
	align-content: center;
	width: 75%;
	background-color: rgb(10, 3, 3);
	border: 5px solid rgb(252, 184, 106);
	border-radius: 7px;
	opacity: 1;
	transition: all 1s ease-in-out;
}
.content-closed {
	/* position: absolute; */
	height: 0px;
	width: 40%;
	align-self: center;
	opacity: 0;
	transition: all 1s ease-in-out;
}
@media (min-aspect-ratio: 16/9) {
	.background-video {
		width: 100%;
		height: auto;
	}
}
@media (max-aspect-ratio: 16/9) {
	.background-video {
		width: auto;
		height: 100%;
	}
}
